.OffCanvas {
	position: fixed;
	bottom:0px;
	width: 330px;
	height: 100vh;
	left: 0px;
	background-color: var(--fill-color);
	border-right: 2px solid var(--icon-color);
	box-shadow: 5px 1px 10px var(--fill-color);
	overflow-x: hidden;
	overflow-y: auto;
	
}

.OffCanvasMainIcon {
	color: var(--heading);
	display: flex;
	justify-content: center;
	font-size: 50px;
	margin-top: -30px;
	margin-bottom: -20px;
}

.OffCanvasMainIcon:hover {
	color: var(--background-color);
}

.OffCanvasCont {
	padding-left: 0px;
	padding-right: 0px;
}
.OffCanvasBorder {
	padding-top: 30px;
	padding-bottom: 10px;
}
.sign {
	font-family: Sign;
	display: flex;
	justify-content: center;
	font-size: 25px;
	font-weight: bolder;
	background: linear-gradient(271deg, #a0e9ff 30%, #a162e8 50%, #f093b0 70%, #edca85 94%);
	background-clip: text;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.isRotated {
	transform: rotate(-90deg);
	margin: -28px 3px 0px 0px;
	transition: 1s;
}

.isNotRotated {
	transition: 1s;
	transform: rotate(0deg);
	margin: -1px 0px 0px 0px;
}

.offCanvasOpenIcon {
	flex: 10%;
}
.signName {
	flex: 90%;
	margin-left: 50px;
}

.OffCanvasMenu {
	overflow-x: hidden;
	overflow-y: auto;
	height: 350px;
	background-color: var(--background-color);
	width: 100%;
	border-top: 1px solid var(--icon-color);
	border-bottom: 1px solid var(--icon-color);
}

li {
	list-style-type: none;
	padding-top: 10px;
	padding-bottom: 10px;
	padding-right: 0px;
	padding-left: 0px;
}
.Active{
	background-color: var(--fill-color);
	

}
a {
	text-decoration: none;
	color: var(--text-color);
}
a:hover {
	color: var(--heading);
}

.OffCanvasHeader {
	background-color: var(--background-color) !important;
}

.OffCanvasMe {
	height: 0px;
	background-color: transparent;
	width: 100%;
	padding-left: 0px;
}

.OffCanvasList {
	display: flex;
	flex-wrap: wrap;
	color: var(--text-color);
}
.OffCanvasList:hover {
	color: var(--heading);
}

.OffCanvasLeft {
	flex: 10%;
}
.OffCanvasRight {
	flex: 70%;
}

.OffCanvasImg {
	margin-left: 05px;
	margin-top: 5px;
}

.secondMenuHello {
	width: 120px;
	height: 170px;
	transition: 2s;
	overflow: hidden;
	position: relative;
	left: 10px;
	top: 00px;
}

.secondMenuHello ul li {
	margin-left: -10px;
}
.secondMenuByee {
	width: 120px;
	height: 0px;
	transition: 2s;
	position: relative;
	left: 10px;
	top: 00px;
}

.secondMenuUL {
	display: none;
}

.secondMenuULShow {
	display: block;
}
.meMenuName {
	padding: 0px;
	margin: 0px;
}

.meMenuOpen {
	width: 300px;
	height: 420px;
	border: 1px solid white;
	border-radius: 1em;
	position: absolute;
	top: 140px;
	left: -05px;
	background-color: var(--background-color);
	padding: 3em;
}

.meMenuInner {
	margin-top: 20px;
}

.meMenuClosed {
	display: none;
}

.meMenuHeader {
	color: var(--heading);
}

@media screen and (max-width: 768px) {
	.meSpan {
		margin-left: 15px;
	}
	.isRotated {
		transform: rotate(-90deg);
		margin: -7px 3px 0px 0px;
		transition: 1s;
	}
	.OffCanvas {
		height: 100vh;
		width: 220px;
		position: fixed;
		bottom: 0px !important;
	}
	.OffIcon {
		background-color: var(--heading);
	}
	
}
