.ProductMenuMain {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: rgba(0, 0, 0, 0.9);
	display: flex;
	justify-content: center;
	align-items: center;
	margin-left: 280px !important;
	padding-top: 10px;
}
.ProductMenu {
	padding: 1em;
}

.ProductHeader {
	color: var(--heading);
	font-weight: bolder;
}

.BorderProduct {
	border: 2px solid transparent;
	border-radius: 1em;
	background-image: linear-gradient(#1f2833, #1f2833),
		linear-gradient(210deg, #0b0c10, #c5c6c7, brown, brown, #45a29e, #0b0c10);
	background-origin: border-box;
	background-clip: content-box, border-box;
	overflow: hidden;
	color: var(--text-color);
	height: 500px ;
	width: 900px;
	animation-name: ProductAnim;
	animation-duration: 1s;
	animation-direction: normal;
	
}
.BorderProduct:hover {
	box-shadow: 5px 5px 5px 9px var(--fill-color);
}
.ProductSales {
	background-image: linear-gradient(210deg, #0b0c10 40%, brown 40%, #1f2833 80%);
	border-radius: 1em;
	height: 100%;
}

@keyframes ProductAnim {
 
	0%{
		width:550px;
		height: 50px;
		transform: rotate(90deg);
	}
	25%{
		transform: rotate(0deg);
	}
	100%{
		height: 500px ;
		width: 900px;
	}
	
}