@font-face {
	font-family: Sign;
	src: url(../../fonts/Pacifico-Regular.ttf);
}
@font-face {
	font-family: Main;
	src: url(../../fonts/Rajdhani-Light.ttf);
}
:root {
	--background-color: #0b0c10;
	--fill-color: #1f2833;
	--heading: #66fcf1;
	--text-color: #c5c6c7;
	--icon-color: #45a29e;
}
* {
	box-sizing: border-box;
}
.HomeBlured {
	opacity: 0.1;
}
.HomeSales {
	background-image: linear-gradient(45deg, #0b0c10 40%, brown 40%, #1f2833 80%);
	border-radius: 1em;
	height: 100%;
}

.HomeNoPadding {
	padding-right: 0px !important;
}
.active {
	padding: 0.2em;
	padding-left: 0.5em;
	border-radius: 12px;
	background-color: var(--fill-color);
}
body {
	margin: 0;
	padding: 0px;
	background-color: var(--background-color);
	background-image: linear-gradient(140deg, #0b0c10, #0b0c10, brown) !important;	
	color: var(--text-color);
	font-family: Main;
}
.noPad {
	padding-right: 0px !important;
	padding-left: 0px;
	z-index: 9999;
}

.HomeCont {
	padding-left: 0px;
}

.homeMenu {
	padding-left: 22px;
	padding-top: 22px;
	color: var(--icon-color);
}
.homeMenu:focus {
	color: var(--fill-color);
}
.HomeHeader {
	color: var(--heading);
}

@media screen and (min-width: 768px) and (max-width: 992px) {
	.HomeColSet {
		padding-left: 50px;
	}
}
@media screen and (max-width: 768px) {
	.HomeColSet {
		padding-left: 50px;
	}
	.smallWidth {
		width: 200px;
		position: fixed;
	}
	.noPad {
		height: 100vh;
	}
	.HomeHeader {
		width: 100%;
		font-size: 17px !important;
	}
	.HomeLoader {
		position: absolute !important;
		right: 80% !important;
		top: 50% !important;
	}
}

@media screen and (min-width: 992px) {
	.HomeColSet {
		position: fixed;
		padding-left: 280px;
	}
}
@media screen and (min-width: 1200px) {
	.HomeColSet {
		position: fixed;
		padding-left: 650px;
	}
}

.b {
	border: 1px solid blue;
}

.Home-Loading {
	position: absolute;
	right: 40%;
	top: 45%;
	transform: translate(-50%, -50%);
}
