.SignUpModalMain {
	position: fixed;
	top: 50%;
	right: 10%;
	transform: translate(-50%, -50%);
	color: white !important;

	background-image: linear-gradient(-45deg, #0b0c10 40%, brown 40%, #1f2833 80%);
	height: 500px;
	width: 500px;
	z-index: 1000 ;
	padding-top: 30px;
	text-align: center;
    opacity: 1;
}

.mainSignUp {
	background-color: rgba(0,0,0,0.9);
	
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
    z-index: 100;
}

.SignUpText {
	width: 230px;
    color:var(--background-color) !important;
    font-weight: bolder;
}


@media screen and (max-width:720px) {
    .SignUpModalMain {
        position: fixed;
        top: 50%;
        right: -50%;
        height: 600px;
        width: 380px;
        
        
    }
    
}