.BorderMain{
    border:2px solid transparent;
    border-radius: 1em;
    background-image: linear-gradient(#1f2833,#1f2833),
    linear-gradient(210deg ,#0b0c10, #c5c6c7 , brown,brown ,#45a29e ,#0b0c10 );
    background-origin: border-box;
    background-clip: content-box,border-box;
    overflow: hidden;
    color:var(--text-color);
}

.BorderMain:hover{
 box-shadow: 5px 5px 5px 9px var(--fill-color);
  
}
